<template>
	<view class="tabber-info">
		<u-tabbar :value="value1" activeColor="#66bbad">
			<!-- <u-tabbar-item v-for="(item,i) in tabbarItemObj" :key="i" :text="item.text" :icon="item.icon" @click="click1(i)" ></u-tabbar-item> -->
			<u-tabbar-item text="首页" icon="home" @click="click1" ></u-tabbar-item>
			<u-tabbar-item text="购物车"  icon="shopping-cart" @click="click1" :badge="num" ></u-tabbar-item>
			<u-tabbar-item text="我的" icon="account" @click="click1" ></u-tabbar-item>
		</u-tabbar>
	</view>
</template>

<script>
	export default {
		props:{
			num:{
				type:Number,
				default:3
			}
		},
		name:"tabbar",
		data() {
			return {
				value1: uni.getStorageSync('activeValue') || 0,
				tabbarItemObj:[
					{text:'首页',icon:'home'},
					{text:'购物车',icon:'shopping-cart'},
					{text:'我的',icon:'account'}
				]
				
			};
		},
		methods:{
			click1(i){
				this.value1 = i
				uni.setStorageSync('activeValue',this.value1)
				if(i === 0) {
						uni.navigateTo({
							url:'/pages/home/home'
						})
					} else if(i === 1) {
						uni.navigateTo({
							url:'/pages/cart/cart'
						})
					} else if(i === 2) {
						uni.navigateTo({
							url:'/pages/user/my'
						})
					}
				// if(i === 1) {
				// 	uni.navigateTo({
				// 		url:'/pages/cart/cart'
				// 	})
				// }
				
			}
		},
		computed:{}
	}
</script>

<style lang="scss">
.tabber-info{
	margin-top: 80rpx;
}
</style>