<template>
	<view class="nav-info">
		<view class="cu-bar" :style="{backgroundColor: navBgColor}">
			<view class="action">
				<!-- 位置 -->
				<view class="flex" @tap="handleAddress()">
					<u-icon name='map' size="22" color="gray" v-if="address"></u-icon>
				</view>
				<!-- 返回首页 -->
				<u-icon  name="home" size="22" color="gray" v-if="homePage"></u-icon>
				<!-- 返回上一页 -->
				<u-icon @click="changeBack()" name="arrow-left" size="22" color="gray" v-if="back"></u-icon>

			</view>
			<view class="content text-bold">
				<text :style="{color:titleColor}">{{title}}</text> 
			</view>

			<view class="action">
				<text class="cuIcon-search text-bold fs-34 padding-right-sm"></text>
				<u-line length="10" direction="col" color="#282c35"></u-line>
				<text class="cuIcon-sort text-bold fs-40 padding-left-sm"></text>
			</view>
		</view>




	</view>
</template>

<script>
	export default {
		props:{
			address:{
				type:Boolean
			},
			homePage:{
				type:Boolean
			},
			back:{
				type:Boolean
			},
			// 标题
			title:{
				type:String
			},
			// 自定义导航栏背景颜色
			navBgColor:{
				type:String,
				default:'#fff'
			},
			// 自定义导航栏标题颜色
			titleColor:{
				type:String,
				default:'black'
			}
		},
		name:"navigationBar",
		data() {
			return {
				
			};
		},
		methods:{
			changeBack(){
				uni.navigateBack({
					delta:1
				})
			}
		}
	}
</script>

<style lang="scss">
.nav-info{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100rpx;
		z-index: 1;
		box-shadow: 0 1px 1px -1px gray;
	}
</style>