import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'


import Vue from 'vue'

// 导入uView js库
import uView from "uview-ui";
Vue.use(uView);

// 导入时间格式处理包
import moment from 'moment'

// 挂载全局组件
import components from 'components/index.js'
Vue.use(components)

// 日期格式过滤器
Vue.filter('moment',function(val){
	return moment(val).format('YYYY-MM-DD')
})

Vue.config.productionTip = false
App.mpType = 'app'

import store from './store/index.js'

const app = new Vue({
    ...App,
	store
})
app.$mount()