<template>
	<view>
		<view class="padding-tb">
			
			<view class="goods-relevant" v-if="similar">
			    <view class="header">
			      <text class="icon"></text> 
			      <span class="title">猜你喜欢</span>
			    </view>
			  </view>
			
			<view class="flex justify-between padding-sm" v-else>
				<view class="">
					<text class="fs-36 text-bold padding-right-xs">{{title}}</text>
					<text class="color-huise fs-14">{{str}}</text>
				</view>
				<view class="" v-if="flag && !similar">
					查看全部 <text class="color-huise cuIcon-right"></text>
				</view>
			</view>
			
			<view class="new-info flex padding-lr-xs">
				<view class="new-con" v-for="(item,i) in list" :key="item.id">
					<view class="">
						<image v-if="flag  && !similar" @click="goodsDetails(item.id)" class="new-img" :src="item.picture" mode="scaleToFill"></image>
						<image v-else class="new-img" @click="goodsDetails(item.id)" :src="item.picture" mode="heightFix"></image>
					</view>
		
					<view class="text-center" v-if="flag">
						<h4 class="padding-xs">{{item.name}}</h4>
						<text class="color-red">￥{{item.price}}</text>
					</view>
					
					<view class="text-center" v-else>
						<text class="fs-30">{{item.title}}</text>
						<view>
							<text class="color-huise fs-12">{{item.alt}}</text>
						</view>
					</view>
				</view>
				</view>
		</view>
	</view>
</template>

<script>
	import {goodsList} from '../api/goods.js'
	export default {
		props:{
			list:{
				type:Array,
				required:true
			},
			title:{
				type:String,
			},
			str:{
				type:String,
			},
			flag:{
				type:Boolean,
				default:false
			},
			similar:{
				type:Boolean,
				default:false
			}
		},
		name:"homeModulesData",
		data() {
			return {
			};
		},
		methods:{
			async goodsDetails(id){
				const res = await goodsList(id)
				uni.navigateTo({
					url:'/pages/goods/goods-info',
					success :(res2) => {
						res2.eventChannel.emit('item',{data:res.data.result})
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	h4 {
		width: 300rpx;
		overflow: hidden; //超出的文本隐藏
		text-overflow: ellipsis; //溢出用省略号显示
		white-space: nowrap; // 默认不换行；
		font-weight: 400;
	}
	
	.new-info {
		overflow: auto;
		width: 100%;
		height: 400rpx;
		.new-con{
			background-color:#f8fafb;
			margin-right: 15rpx;
			.new-img {
				width: 100%;
				height: 300rpx;
			}	
		}
		
	}
	
	// 同类商品
	.goods-relevant {
	  background: #fff;
	  margin-top: 20px;
	  overflow: auto;
	  .header {
	    height: 80px;
	    line-height: 80px;
	    padding: 0 20px;
	    .title {
	      font-size: 20px;
	      padding-left: 10px;
	    }
	    .icon {
	      width: 16px;
	      height: 16px;
	      display: inline-block;
	      border-top: 4px solid #27BA9B;
	      border-right: 4px solid #27BA9B;
	      box-sizing: border-box;
	      position: relative;
	      transform: rotate(45deg);
	      &::before {
	        content: "";
	        width: 10px;
	        height: 10px;
	        position: absolute;
	        left: 0;
	        top: 2px;
	        background: lighten(#27BA9B, 40%);
	      }
	    }
	  }
	}
</style>